function ListItemLink(props) {
  return <ListItem button component="a" {...props} />;
}

const Accordion = withStyles({
  root: {
    border: '1px solid rgba(0, 0, 0, .125)',
    boxShadow: 'none',
    '&:not(:last-child)': {
      borderBottom: 0,
    },
    '&:before': {
      display: 'none',
    },
    '&$expanded': {
      margin: 'auto',
    },
  },
  expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
  root: {
    backgroundColor: 'rgba(0, 0, 0, .03)',
    borderBottom: '1px solid rgba(0, 0, 0, .125)',
    marginBottom: -1,
    minHeight: 56,
    '&$expanded': {
      minHeight: 56,
    },
  },
  content: {
    '&$expanded': {
      margin: '12px 0',
    },
  },
  expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiAccordionDetails);

const ShapesShowcase = () => {
  const classes = useStyles();
  const [url, setUrl] = useState(videoMP4);
  const circle = <div className={clsx(classes.shape, classes.shapeCircle)} />;


  const [expanded, setExpanded] = React.useState('panel1');

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  return(
    <Box>
    <Container className={classes.containerSection}>

      <Container maxWidth="lg">
        <Grid container spacing={4} direction="row" alignItems="center" className={classes.artDiv}>
          <Grid item xs={12} sm={12} md={5} className={classes.artDiv}>
          <div className={classes.artDiv} alignItems="center">
            <Paper className={classes.paperFrame} >
              <div className="boxybox">
                  <img width='100%' height='100%' src={coins} alt="Monke" sx={{ }} />
              </div>
            </Paper>
          </div>
          </ Grid>
          <Grid item xs={12} sm={12} md={7} order={{ xs: 2, sm: 2 }} className={classes.artDiv}>
            <Container maxWidth="md" variant="outlined">
            <Typography component="h1" variant="h2" align="center" color="textPrimary"  className={classes.titlesection} gutterBottom>
              <strong>ForeverCharity</strong>
            </Typography>

            <Typography variant="h6" align="center" color="textSecondary" gutterBottom>

            100% of royalties from GodRoll projects goes to the ForeverCharity.<br />
            10% of all mint sales goes to the ForeverCharity.
            <br /><br />
            All funds will be invested using the Solana ecosystems protocols to earn a yeild. That yeild will then be spent monthly to buy toys for children in need around the world.
            <br /><br />
            The goal is to grow this to a level where we are sending impactful amounts of toys, for the world to take notice that crypto and solana changes the way we do everything.
            <br /><br />
            <strong>Donate once, have it give forever!</strong>

            </Typography>
            <Button variant="outlined" color="primary" style={{marginTop: '20px'}} disableElevation>
                  Waiting for JustShapes mint
                </Button>




              </Container>
            </ Grid>

          </ Grid>
      </ Container>
      </ Container>
    </Box>
  );
}

export default ShapesShowcase;
