import { makeStyles } from '@material-ui/core/styles';
import React, { useState, useCallback } from "react";
import ReactDOM from "react-dom";
import ReactPlayer from 'react-player'
import Chip from '@material-ui/core/Chip';
//import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import videoMP4 from "../assets/videos/597.mp4";
import Badge from '@material-ui/core/Badge';
import clsx from 'clsx';

import { withStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import MuiAccordion from '@material-ui/core/Accordion';
import MuiAccordionSummary from '@material-ui/core/AccordionSummary';
import MuiAccordionDetails from '@material-ui/core/AccordionDetails';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import CheckIcon from '@material-ui/icons/Check';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    color: '#000000 !important',
    fontWeight: '500 !important',
    border:'0px solid red',
  },
  dividerStyle:{
    margin: theme.spacing(4),
  },
  container:{
    backgroundColor: '#ffffff !important',
    padding: theme.spacing(10, 0, 10),
  },
  containerSection:{
    backgroundColor: '#ffffff !important',
    padding: theme.spacing(5, 0, 5),
  },
  button:{
    width:'100%',
    justifyContent: 'center',
    padding: theme.spacing(2, 0, 2),

  },
  titlesection:{
    fontWeight: 800,

    fontFamily: 'IBM Plex Sans, sans-serif',
    color:'#000',

  },
  paperFrame:{
    border: '0px solid',
    boxShadow:'0 8px 30px rgba(0,0,0,0.32)',
    borderRadius:'5px',
    p:0,
    lineHeight:0,
    overflow:'hidden',
  },
  artDiv:{
    border:'0px solid red',

  },
  headerHero:{
    fontWeight: 800,

    fontFamily: 'IBM Plex Sans, sans-serif'
  },
  chipRed:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#EA3323',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipBlue:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#007CF3',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipGreen:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#1FB254',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipYellow:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#FFBB25',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipPurple:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#9c78ff',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipOrange:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#ff8c01',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipWhite:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#ffffff',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipBlack:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#000000',
    color:'#ffffff',
    border:'1px solid #333',
  },
}));

function ListItemLink(props) {
  return <ListItem button component="a" {...props} />;
}

const Accordion = withStyles({
  root: {
    border: '1px solid rgba(0, 0, 0, .125)',
    boxShadow: 'none',
    '&:not(:last-child)': {
      borderBottom: 0,
    },
    '&:before': {
      display: 'none',
    },
    '&$expanded': {
      margin: 'auto',
    },
  },
  expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
  root: {
    backgroundColor: 'rgba(0, 0, 0, .03)',
    borderBottom: '1px solid rgba(0, 0, 0, .125)',
    marginBottom: -1,
    minHeight: 56,
    '&$expanded': {
      minHeight: 56,
    },
  },
  content: {
    '&$expanded': {
      margin: '12px 0',
    },
  },
  expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiAccordionDetails);

const ShapesShowcase = () => {
  const classes = useStyles();
  const [url, setUrl] = useState(videoMP4);
  const circle = <div className={clsx(classes.shape, classes.shapeCircle)} />;


  const [expanded, setExpanded] = React.useState('panel1');

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  return(
    <Box>
    <Container className={classes.containerSection}>

      <Container maxWidth="lg">
        <Grid container spacing={4} direction="row" alignItems="center" className={classes.artDiv}>
          <Grid item xs={12} sm={12} md={6} className={classes.artDiv}>
          <div className={classes.artDiv} alignItems="center">
            <Paper className={classes.paperFrame} >
              <div className="boxybox">
                <ReactPlayer width='100%' height='100%' playing loop muted={true} controls={false} playsinline={true} url={url} type="video/mp4" />
              </div>
            </Paper>
          </div>
          </ Grid>
          <Grid item xs={12} sm={12} md={6} order={{ xs: 2, sm: 2 }} className={classes.artDiv}>
            <Container maxWidth="md" variant="outlined">
            <Typography component="h1" variant="h2" align="center" color="textPrimary"  className={classes.titlesection} gutterBottom>
              <strong>ForeverShapes</strong>
            </Typography>
            <Typography component="h1" variant="h5" align="center" color="textPrimary" gutterBottom>
              <strong>Concept </strong>
            </Typography>

            <Typography variant="h6" align="center" color="textSecondary" gutterBottom>
            Create a quality generative art collection for Solana.<br />
            Balanced styles, patterns, colors, and rarities.<br />
            Decent roadmap, benefits for holders.
            </Typography>
            <br />
            <Typography component="h1" variant="h5" align="center" color="textPrimary" gutterBottom>
              <strong>Result </strong>
            </Typography>

            <Typography variant="h6" align="center" color="textSecondary" gutterBottom>
            60FPS, Seamless Looping,<br /> 10 Second, Generative Art.



            </Typography>

            <Button  href="https://shapes.foreverdao.org" variant="contained" color="primary" style={{marginTop: '20px'}} disableElevation>
              VIEW PROJECT / MINT
            </Button>
            </Container>
          </ Grid>
        </ Grid>
      </ Container>
      </ Container>
    </Box>
  );
}

export default ShapesShowcase;
