import { makeStyles, createTheme, responsiveFontSizes, ThemeProvider } from '@material-ui/core/styles';
import thug from "../assets/images/thug.png";
import monke from "../assets/images/monke.png";
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';

const useStyles = makeStyles((theme) => ({

  root: {
    width: '100%',
    backgroundColor: '#ffffff',

  },

  listHeader:{
    textAlign: 'center',
    fontSize: '1.8rem',
    color: '#000000',
    fontWeight: 300,
    fontFamily: 'Inter, sans-serif',
    padding: theme.spacing(2, 0, 1),

  },
  headerHero:{
    fontWeight: 800,

    fontFamily: 'IBM Plex Sans, sans-serif',
    color:'#000',

  },
  subHeaderHero:{
    fontWeight: 100,
    fontSize:'4.1rem',
    fontFamily: 'Lobster, cursive',
    color:'#000',
  },
  container:{
    backgroundColor: '#ffffff !important',
    padding: theme.spacing(8, 0, 4),
  },
  paperFrame:{
    border: '0px solid',
    boxShadow:'0 8px 30px rgba(0,0,0,0.18)',
    borderRadius:'5px',
    p:0,
    lineHeight:0,
  },
  titleShapes:{
    fontWeight: 800,
    fontFamily: 'IBM Plex Sans, sans-serif',
    color:'#000',
  },
}));

const Navbar = () => {
  const classes = useStyles();
  return(
    <Box>
      <Container maxWidth="sm" sx={{pt:8, pb:8}}>
        <Grid container spacing={4} direction="row" alignItems="center">
          <Grid item xs={6} sm={6} md={6}>
            <Paper sx={{boxShadow:'0 8px 30px rgba(0,0,0,0.12)', borderRadius:'5px', p:2, lineHeight:0}} >
              <img width='100%' height='100%' src={thug} alt="Thug" sx={{ }} />
            </Paper>
          </Grid>
          <Grid item xs={6} sm={6} md={6}>
            <Paper sx={{boxShadow:'0 8px 30px rgba(0,0,0,0.12)', borderRadius:'5px', p:2, lineHeight:0}} >
              <img width='100%' height='100%' src={monke} alt="Monke" sx={{ }} />
            </Paper>
          </Grid>
        </Grid>
      </Container>
      <Container align="center" maxWidth="md" sx={{pt: 0, pb: 6,}}>
        <Typography gutterBottom variant="h5"
        align="center"
        color="text.primary"
        gutterBottom
        >
          <strong>Two brothers obsessed with Solana.</strong>
        </Typography>
        <Typography variant="h6" align="center" color="text.secondary" gutterBottom>
          No hype, focused on building and delivering great experieces.<br />
          ForeverDAO, ForeverShapes, ForeverCoins.<br />
        </Typography>




      </Container>
    </Box>
  );
}

export default Navbar;
